@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: "Quicksand", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

html,
body,
#root {
  height: 100%; }

button {
  font-family: "Quicksand", sans-serif; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

p {
  font-weight: 500;
  color: #002b45; }

h1 {
  font-size: 2rem;
  font-weight: 900;
  color: #002b45;
  position: relative;
  margin-top: 0; }
  h1.h1-sub::after {
    content: "";
    height: 4px;
    width: 50px;
    position: absolute;
    bottom: -10px;
    left: 0;
    background-color: #40c3df; }

h3 {
  font-size: 1rem;
  font-weight: 900;
  color: #002b45;
  margin: 0; }

h5 {
  margin: 0; }

label {
  color: #002b45;
  font-weight: 700;
  font-size: 0.9rem;
  display: inline-block; }

* {
  box-sizing: border-box; }

.App {
  background-color: #f3f3f3;
  position: relative;
  min-height: 100%; }
  .App-header {
    position: absolute;
    background-color: #e3e9e9;
    width: 100%;
    height: 70px;
    z-index: 10; }
  .App-content {
    padding: 20px 10px; }

.Wizard {
  width: 100%;
  max-width: 950px;
  background-color: #ffffff;
  position: relative;
  z-index: 20;
  margin: 0 auto; }
  .Wizard-header {
    background-color: #ccd5da;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center; }
  .Wizard-content {
    box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.2); }
    .Wizard-content .ProductInfo-content,
    .Wizard-content .Feedback-content,
    .Wizard-content .Form-content {
      padding: 35px 50px; }
      @media (max-width: 500px) {
        .Wizard-content .ProductInfo-content,
        .Wizard-content .Feedback-content,
        .Wizard-content .Form-content {
          padding: 35px 15px; } }

.Stepper {
  display: flex;
  justify-content: center;
  align-items: center; }

.Step {
  width: 26px;
  height: 26px;
  border-radius: 1000px;
  background-color: #788f9c;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  margin: 0 -5px;
  position: relative;
  z-index: 999;
  font-size: 0.9rem; }
  .Step-completed {
    background-color: #ff0049;
    -webkit-transition: -webkit-transform 0.3s ease-in-out;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out; }
  .Step-active {
    background-color: #002b45;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.3); }
  .Step-bar {
    width: 50px;
    height: 3px;
    background-color: #788f9c; }
    .Step-bar-completed {
      background-color: #ff0049; }

.Feedback h1 {
  margin: 0; }
  @media (max-width: 500px) {
    .Feedback h1 {
      margin: 10px 0; } }

.Feedback p {
  margin-top: 10px; }

.Button,
.Button-filled {
  padding: 12px 30px 12px 30px;
  border: none;
  font-weight: 900;
  font-size: 1rem;
  cursor: pointer;
  outline: none; }

.Button {
  background-color: transparent;
  padding: 10px 20px 10px 0px; }
  .Button.color-primary {
    color: #ff0049; }
  .Button.color-secondary {
    color: #002b45; }
  .Button-filled {
    -webkit-transition: background-color 0.2s ease-in-out;
    transition: background-color 0.2s ease-in-out;
    background-color: #002b45;
    color: #ffffff; }
    .Button-filled:disabled {
      -webkit-transition: background-color 0.2s ease-in-out;
      transition: background-color 0.2s ease-in-out;
      background-color: #8fb0c4;
      cursor: auto; }
  .Button-endAddornment {
    margin-top: 2px;
    display: inline-block;
    vertical-align: middle; }
    @media (max-width: 500px) {
      .Button-endAddornment {
        display: none; } }

.ButtonContainer {
  height: 130px;
  border-top: 2px solid #f3f3f3;
  padding: 0 50px; }
  .ButtonContainer-submit {
    text-align: right; }
    @media (max-width: 500px) {
      .ButtonContainer-submit {
        padding-left: 0 !important; } }
  .ButtonContainer-cancel {
    text-align: left; }
    @media (max-width: 500px) {
      .ButtonContainer-cancel {
        padding-right: 0 !important; } }
  @media (max-width: 500px) {
    .ButtonContainer {
      padding: 0 15px; } }

.TextField {
  width: 100%; }
  @media (max-width: 500px) {
    .TextField {
      margin-top: 15px; } }
  .TextField-label {
    width: 100%; }
  .TextField-container {
    margin-top: 7px;
    position: relative; }
  .TextField-input {
    font-family: "Quicksand", sans-serif;
    width: 100%;
    border-radius: 1px;
    height: 45px;
    border: 1px solid #c5d0d3;
    padding: 3px 45px 3px 10px;
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: normal;
    -webkit-transition: border 0.1s ease-in-out;
    transition: border 0.1s ease-in-out; }
    .TextField-input[type="password"] {
      padding-right: 50px;
      font-size: 1.8rem;
      letter-spacing: 2px; }
    .TextField-input-error {
      border: 1px solid #ad1b08; }
    .TextField-input:focus {
      border: 2px solid #002b45;
      outline: none;
      -webkit-transition: border 0.1s ease-in-out;
      transition: border 0.1s ease-in-out; }
    .TextField-input::-webkit-input-placeholder {
      font-size: 1rem;
      letter-spacing: normal;
      font-weight: 600;
      position: absolute;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%); }
    .TextField-input::-moz-placeholder {
      font-size: 1rem;
      letter-spacing: normal;
      font-weight: 600;
      position: absolute;
      top: 50%;
      transform: translateY(-50%); }
    .TextField-input:-ms-input-placeholder {
      font-size: 1rem;
      letter-spacing: normal;
      font-weight: 600;
      position: absolute;
      top: 50%;
      transform: translateY(-50%); }
    .TextField-input::-ms-input-placeholder {
      font-size: 1rem;
      letter-spacing: normal;
      font-weight: 600;
      position: absolute;
      top: 50%;
      transform: translateY(-50%); }
    .TextField-input::placeholder {
      font-size: 1rem;
      letter-spacing: normal;
      font-weight: 600;
      position: absolute;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%); }
  .TextField-length-counter {
    width: 100%;
    text-align: right;
    display: inline-block;
    margin-top: 5px;
    font-size: 0.8rem;
    font-weight: 100; }
  .TextField-error {
