.VisibilityButton {
  background-color: transparent;
  border: none;
  position: absolute;
  right: 7px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
  outline: none; }

.Features {
  margin: 60px 0; }
  .Features-column {
    text-align: center; }
    @media (max-width: 500px) {
      .Features-column {
        margin-top: 15px; } }
    .Features-column p {
      padding: 0 20px; }

.Checkbox {
  min-width: 22px;
  height: 22px;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  position: relative;
  border-radius: 3px;
  border: 2px solid #002b45;
  margin: 0;
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  outline: none; }
  .Checkbox::after {
    content: none; }
  .Checkbox:checked {
    background-color: #002b45;
    -webkit-transition: all 0.15s ease-in-out;
    transition: all 0.15s ease-in-out; }
    .Checkbox:checked::after {
      content: url(/openbank-test/static/media/checked.c76c9eb3.svg);
      position: absolute;
      top: 51%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%); }
  .Checkbox-container {
    display: flex;
    align-items: center; }
    .Checkbox-container > * {
      cursor: pointer; }
  .Checkbox-label {
    margin-left: 10px;
    font-weight: 500; }

